body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.h1-clip {
    font-size: 76px;
    background: -webkit-linear-gradient(left, red, #e4b8b7);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-family: 'Montserrat', sans-serif;
    font-weight: 900;
}
.search {
    width: 500px;
    height: 40px;
    margin: 50px auto;
    background: #444;
    background: rgba(0, 0, 0, .2);
    border-radius: 3px;
    border: 1px solid #fff;
}

.search input {
    width: 370px;
    padding: 10px 5px;
    text-align: center;
    float: left;
    color: #ccc;
    border: 0;
    background: transparent;
    border-radius: 3px 0 0 3px;
}

.search input:focus {
    outline: 0;
    background: transparent;
}

.search button {
    position: relative;
    float: right;
    border: 0;
    padding: 0;
    cursor: pointer;
    height: 40px;
    width: 120px;
    color: #fff;
    background: transparent;
    border-left: 1px solid #fff;
    border-radius: 0 3px 3px 0;
}

.search button:hover {
    background: #fff;
    color: #444;
}

.search button:active {
    box-shadow: 0px 0px 12px 0px rgba(225, 225, 225, 1);
}

.search button:focus {
    outline: 0;
}
.App {
  text-align: center;
}

.fav-wrapper > * {
  pointer-events: none;
  cursor: pointer;
}

.fav-wrapper {
  cursor: pointer;
}

.grow-hov:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.header-height {
  height: 90vh;
}

